import NewsApi from '../../scripts/API/News.Api';

/**
 * class SelectBottom
 * @extends NewsApi
 * */
class SelectBottom extends NewsApi {
    /**
     * Create selectForNewsAPI
     * @param {HTMLElement} selectTop
     * @param {HTMLElement} selectBottom
     * @param {HTMLElement} cardsRow
     * @param {ChildNode} topLabel
     * @param {HTMLElement} mainInput
     * */
    constructor(selectTop, selectBottom, cardsRow, topLabel, mainInput) {
        super();
        this.selectTop = selectTop;
        this.selectBottom = selectBottom;
        this.cardsRow = cardsRow;
        this.topLabel = topLabel;
        this.mainInput = mainInput;
        this.countryValue = 'ru';
    }

    selectTopTracking() {
        this.selectTop.addEventListener('change', () => {
            const { value } = this.selectTop;
            if (value === 'ru') {
                this.getRussianLanguage();
                this.countryValue = 'ru';
                this.getNewsCallBack();
            }
            if (value === 'us') {
                this.getEnglishLanguage();
                this.countryValue = 'us';
                this.getNewsCallBack();
            }
            if (value === 'it') {
                this.getItalianLanguage();
                this.countryValue = 'it';
                this.getNewsCallBack();
            }
            if (value === 'fr') {
                this.getFrenchLanguage();
                this.countryValue = 'fr';
                this.getNewsCallBack();
            }
            if (value === 'de') {
                this.getGermanLanguage();
                this.countryValue = 'de';
                this.getNewsCallBack();
            }
        });
    }

    getRussianLanguage() {
        this.topLabel.nodeValue = 'Пользовательские настройки';
        this.selectTop.innerHTML = `
            <optgroup label="Выберите тему">
                <option value="default">По умолчанию</option>
                <option value="light">Светлая тема</option>
                <option value="dark">Темная тема</option>
            </optgroup>
            <optgroup label="Выберите язык">
                <option value="ru">Русский</option>
                <option value="us">Английский</option>
                <option value="it">Итальянский</option>
                <option value="fr">Французский</option>
                <option value="de">Немецкий</option>
            </optgroup>
        `;
        this.selectTop.value = 'ru';
        this.selectBottom.innerHTML = `
            <optgroup label="Выберите категорию">
                <option value="business">Бизнес</option>
                <option value="entertainment">Развлечения</option>
                <option value="general">Основное</option>
                <option value="health">Здоровье</option>
                <option value="science">Наука и техника</option>
                <option value="sports">Спорт</option>
                <option value="technology">Технологии</option>
            </optgroup>
        `;
        this.mainInput.removeAttribute('placeholder');
        this.mainInput.setAttribute('placeholder', 'Поиск новостей');
    }

    getEnglishLanguage() {
        this.topLabel.nodeValue = 'User settings';
        this.selectTop.innerHTML = `
            <optgroup label="Select a topic">
                <option value="default">Default</option>
                <option value="light">light theme</option>
                <option value="dark">Dark theme</option>
            </optgroup>
            <optgroup label="Select a language">
                <option value="ru">Russian</option>
                <option value="us">English</option>
                <option value="it">Italian</option>
                <option value="fr">French</option>
                <option value="de">German</option>
            </optgroup>
        `;
        this.selectTop.value = 'us';
        this.selectBottom.innerHTML = `
            <optgroup label="Select a category">
                <option value="business">Business</option>
                <option value="entertainment">Entertainments</option>
                <option value="general">Main</option>
                <option value="health">Health</option>
                <option value="science">Science</option>
                <option value="sports">Sport</option>
                <option value="technology">Technology</option>
            </optgroup>
        `;
        this.mainInput.removeAttribute('placeholder');
        this.mainInput.setAttribute('placeholder', 'Search for news');
    }

    getItalianLanguage() {
        this.topLabel.nodeValue = 'Ricerca di notizie';
        this.selectTop.innerHTML = `
            <optgroup label="Scegli un tema">
                <option value="default">Di default</option>
                <option value="light">Tema chiaro</option>
                <option value="dark">Tema scuro</option>
            </optgroup>
            <optgroup label="Seleziona la lingua">
                <option value="ru">Russo</option>
                <option value="us">Inglese</option>
                <option value="it">Italiano</option>
                <option value="fr">Francese</option>
                <option value="de">Tedesco</option>
            </optgroup>
        `;
        this.selectTop.value = 'it';
        this.selectBottom.innerHTML = `
            <optgroup label="Seleziona una categoria">
                <option value="business">Business</option>
                <option value="entertainment">Intrattenimenti</option>
                <option value="general">Principale</option>
                <option value="health">Salute</option>
                <option value="science">Scienza</option>
                <option value="sports">Sport</option>
                <option value="technology">Tecnologie</option>
            </optgroup>
        `;
        this.mainInput.removeAttribute('placeholder');
        this.mainInput.setAttribute('placeholder', 'Ricerca di notizie');
    }

    getFrenchLanguage() {
        this.topLabel.nodeValue = 'Paramètres personnalisés';
        this.selectTop.innerHTML = `
            <optgroup label="Выберите тему">
                <option value="default">Tacitement</option>
                <option value="light">Thème lumineux</option>
                <option value="dark">Thème sombre</option>
            </optgroup>
            <optgroup label="Выберите язык">
                <option value="ru">Russe</option>
                <option value="us">Anglais</option>
                <option value="it">Italien</option>
                <option value="fr">Français</option>
                <option value="de">Allemand</option>
            </optgroup>
        `;
        this.selectTop.value = 'fr';
        this.selectBottom.innerHTML = `
            <optgroup label="Sélectionnez une catégorie">
                <option value="business">Affaires</option>
                <option value="entertainment">Loisir</option>
                <option value="general">L'essentiel</option>
                <option value="health">Santé</option>
                <option value="science">Science</option>
                <option value="sports">Sport</option>
                <option value="technology">Technologies</option>
            </optgroup>
        `;
        this.mainInput.removeAttribute('placeholder');
        this.mainInput.setAttribute('placeholder', 'Recherche de nouvelles');
    }

    getGermanLanguage() {
        this.topLabel.nodeValue = 'Benutzerdefinierte Einstellungen';
        this.selectTop.innerHTML = `
            <optgroup label="Wählen Sie ein Thema">
                <option value="default">Stillschweigend</option>
                <option value="light">Helles Thema</option>
                <option value="dark">Dunkles Thema</option>
            </optgroup>
            <optgroup label="Sprache auswählen">
                <option value="ru">Russisch</option>
                <option value="us">Englisch</option>
                <option value="it">Italienisch</option>
                <option value="fr">Französisch</option>
                <option value="de">Deutsch</option>
            </optgroup>
        `;
        this.selectTop.value = 'de';
        this.selectBottom.innerHTML = `
            <optgroup label="Kategorie auswählen">
                <option value="business">Geschäft</option>
                <option value="entertainment">Unterhaltungen</option>
                <option value="general">Grundlegende</option>
                <option value="health">Gesundheit</option>
                <option value="science">Wissenschaft</option>
                <option value="sports">Sport</option>
                <option value="technology">Technologien</option>
            </optgroup>
        `;
        this.mainInput.removeAttribute('placeholder');
        this.mainInput.setAttribute('placeholder', 'Nachrichten suchen');
    }

    getNewsCallBack() {
        const country = this.countryValue;
        const category = this.selectBottom.value;
        const keywords = this.mainInput.value;
        this.getNews(country, category, keywords).catch((err) => {
            throw new Error(err);
        });
    }

    selectBottomTracking() {
        this.selectBottom.addEventListener('change', () => {
            this.getNewsCallBack();
        });
    }

    mainInputTracking() {
        let timeout;
        this.mainInput.addEventListener('input', () => {
            clearTimeout(timeout);
            const country = this.countryValue;
            const category = this.selectBottom.value;
            const keywords = this.mainInput.value;
            timeout = setTimeout(() => {
                this.getNews(country, category, keywords).catch((err) => {
                    throw new Error(err);
                });
            }, 2000);
        });
    }
}

const selectForNewsAPI = new SelectBottom(
    document.getElementById('header-select'),
    document.getElementById('select-bottom'),
    document.getElementById('cards-row'),
    document.getElementById('user-settings').firstChild,
    document.getElementById('main-input'),
);

selectForNewsAPI.selectTopTracking();
selectForNewsAPI.selectBottomTracking();
selectForNewsAPI.mainInputTracking();
