/**
 * class NewsApi
 * */
export default class NewsApi {
    /**
     * async method getNews
     * @param {string} country
     * @param {string} category
     * @param {string} keywords
     * @return {Promise}
     * */
    async getNews(country, category, keywords) {
        try {
            const response = await fetch(
                `https://newsapi.org/v2/top-headlines?q=${keywords}&country=${country}&category=${category}&apiKey=b7ccbb5f6d9f49189d590196c9fa0875`,
            );
            if (response.ok) {
                const extract = response.json();
                NewsApi.renderCard(extract).catch((err) => {
                    throw new Error(err);
                });
            }
        } catch (err) {
            throw new Error(err);
        }
    }

    /**
     * static method renderCard
     * @param {Promise} extract
     * @return {Promise}
     * */
    static renderCard(extract) {
        const cardsRow = document.getElementById('cards-row');
        cardsRow.innerHTML = '';
        return Promise.resolve(extract).then((res) => {
            const { articles } = res;
            articles.forEach((news) => {
                const {
                    title,
                    description,
                    source: { name },
                    url,
                    urlToImage,
                } = news;
                const notFoundImg = '../../images/image-not-found.jpg';
                const card = `
                    <article class="card  main__card">
                        <header class="card__header">
                            <h2 class="heading card__heading"> ${title} </h2>
                        </header>
                        <img 
                            class="card__image" 
                            src="${urlToImage || notFoundImg}"
                            alt="news-image"
                        >
                        <section class="card__body">
                            ${description || ''}
                        </section>
                        <footer class="card__footer">
                            <address class="card__author">
                                ${name}
                            </address>
                            <a href="${url}" target="_blank">
                            <button class="button card__button"> 
                                Source
                            </button>
                            </a>
                        </footer>
                    </article>
                `;
                cardsRow.insertAdjacentHTML('beforeend', card);
            });
            if (!cardsRow.children.length) {
                cardsRow.innerHTML = `
                    <h2 class="heading"> NEWS NOT FOUND </h2>
                `;
            }
        });
    }

    /**
     * static method firstLoadingNews
     * */
    static firstLoadingNews() {
        const response = fetch(
            `https://newsapi.org/v2/top-headlines?country=ru&apiKey=b7ccbb5f6d9f49189d590196c9fa0875`,
        );
        response.then((res) => {
            NewsApi.renderCard(res.json()).catch((err) => {
                throw new Error(err);
            });
        });
    }
}

NewsApi.firstLoadingNews();
