/**
 * class SelectTop
 * */
class SelectTop {
    /**
     * create selectChangeTheme
     * @param {HTMLElement} select
     * @param {HTMLElement} wrapper
     * @param {HTMLElement} header
     * @param {HTMLElement} cardsRow
     * */
    constructor(select, wrapper, header, cardsRow) {
        this.select = select;
        this.wrapper = wrapper;
        this.header = header;
        this.cardsRow = cardsRow;
    }

    tracking() {
        this.loadingTheme();
        this.select.addEventListener('change', () => {
            const { value } = this.select;
            if (value === 'default') this.getDefaultTheme();
            if (value === 'light') this.getLightTheme();
            if (value === 'dark') this.getDarkTheme();
        });
    }

    loadingTheme() {
        if (localStorage.getItem('light')) {
            this.wrapper.className = localStorage.getItem('wrapper-light');
            this.header.className = localStorage.getItem('header-light');
            this.cardsRow.className = localStorage.getItem('cards-row-light');
            this.select.value = this.select[1].value;
        }
        if (localStorage.getItem('dark')) {
            this.wrapper.className = localStorage.getItem('wrapper-dark');
            this.header.className = localStorage.getItem('header-dark');
            this.cardsRow.className = localStorage.getItem('cards-row-dark');
            this.select.value = this.select[2].value;
        }
    }

    getDefaultTheme() {
        this.getDefaultThemeForWrapper();
        this.getDefaultThemeForHeader();
        this.getDefaultThemeForCardsRow();
        localStorage.clear();
        localStorage.setItem('default', 'default-theme');
        localStorage.setItem(
            'wrapper-default',
            (this.wrapper.className = 'wrapper'),
        );
        localStorage.setItem(
            'header-default',
            (this.header.className = `header header_background_aqua 
            header_shadow_gray border 
            border_position_bottom border_color_purple`),
        );
        localStorage.setItem(
            'cards-row-default',
            (this.cardsRow.className = 'main__row main__row_color_light'),
        );
    }

    getDefaultThemeForWrapper() {
        this.wrapper.className = 'wrapper';
    }

    getDefaultThemeForHeader() {
        this.header.className = `header header_background_aqua 
            header_shadow_gray border 
            border_position_bottom border_color_purple`;
    }

    getDefaultThemeForCardsRow() {
        this.cardsRow.className = 'main__row main__row_color_light';
    }

    getLightTheme() {
        this.getLightThemeForWrapper();
        this.getLightThemeForHeader();
        this.getLightThemeForCardsRow();
        localStorage.clear();
        localStorage.setItem('light', 'light-theme');
        localStorage.setItem(
            'wrapper-light',
            (this.wrapper.className = 'wrapper wrapper_background_light'),
        );
        localStorage.setItem(
            'header-light',
            (this.header.className = `header header_background_light 
            header_shadow_gray border 
            border_position_bottom border_color_purple`),
        );
        localStorage.setItem(
            'cards-row-light',
            (this.cardsRow.className = 'main__row main__row_color_dark'),
        );
    }

    getLightThemeForWrapper() {
        this.wrapper.className = 'wrapper wrapper_background_light';
    }

    getLightThemeForHeader() {
        this.header.className = `header header_background_light 
            header_shadow_gray border 
            border_position_bottom border_color_purple`;
    }

    getLightThemeForCardsRow() {
        this.cardsRow.className = 'main__row main__row_color_dark';
    }

    getDarkTheme() {
        this.getDrkThemeForWrapper();
        this.getDrkThemeForHeader();
        this.getDrkThemeForCardsRow();
        localStorage.clear();
        localStorage.setItem('dark', 'dark-theme');
        localStorage.setItem(
            'wrapper-dark',
            (this.wrapper.className = 'wrapper wrapper_background_dark'),
        );
        localStorage.setItem(
            'header-dark',
            (this.header.className = `header header_background_dark 
            header_shadow_gray border 
            border_position_bottom border_color_purple`),
        );
        localStorage.setItem(
            'cards-row-dark',
            (this.cardsRow.className = 'main__row main__row_color_light'),
        );
    }

    getDrkThemeForWrapper() {
        this.wrapper.className = 'wrapper wrapper_background_dark';
    }

    getDrkThemeForHeader() {
        this.header.className = `header header_background_dark 
            header_shadow_gray border 
            border_position_bottom border_color_purple`;
    }

    getDrkThemeForCardsRow() {
        this.cardsRow.className = 'main__row main__row_color_light';
    }
}

const selectChangeTheme = new SelectTop(
    document.getElementById('header-select'),
    document.querySelector('.wrapper'),
    document.querySelector('.header'),
    document.getElementById('cards-row'),
);

selectChangeTheme.tracking();
